import React from 'react';
import Paper from '@mui/material/Paper';
import CardContent from "@mui/material/CardContent";
import Container from '@mui/material/Container';

// Function is called, the return value will end up in x
let width = window.screen.width;
let x = myFunction(width);
function myFunction(width) {
// Function returns the product of a and b
  return Math.floor(width / 2);
}

const Diversity = () => {

  return (
    <div style={{padding: 10, Spacing: 2, textAlign:'center'}}>

      <Paper sx={{bgcolor: '#FEF1E0'}} elevation={24}>
        <header>
              <h1>Diversity, Inclusion, Equity</h1>
        </header>
            <h2></h2>
            <div>
                <img maxWidth={x} style={{ width: {x}, height: "auto" }} src="https://i.makeagif.com/media/7-03-2018/oPPhdD.gif" />
            </div>
            <Container>
                <img src="https://media.tenor.com/41cQ5mDZpREAAAAi/come-on-youre-kidding-right.gif" />
            </Container>
            <h1>{x}</h1>
      </Paper>      
    </div>
  );
}

export default Diversity;